import React, { Component } from 'react';
import './App.css';
import JSONTree from 'react-json-tree';
import ReactFileReader from 'react-file-reader';

const json = {
  array: [1, 2, 3],
  bool: true,
  object: {
    foo: 'bar'
  }
}
/*
function DivConfigJson({Text}){

  return (
    <div className="Div-border-blue Div-side1">
      {Text}
      <JSONTree data = {json}/>
    </div>
  )
}*/


class DivTreeView extends Component{
  render(){
    return(
      <div className="Div-border-red Div-side2">
        <JSONTree data = {json}/>
              
      </div>
    )
  }
}

class DivSettings extends Component{
  state={
    jsonInputSet : false,
    jsonModelSet : false
  };
  /*constructor(){
    super();
    this.

    //this.showFile = this.showFile.bind(this)
    //this.togglejsonInputSet = this.togglejsonInputSet.bind(this)
    this.handleFilesInput = this.handleFilesInput.bind(this)
    this.handleFilesModel = this.handleFilesModel.bind(this)
  }
*/
  handleFilesInput = (files) =>{
    
    console.log(files)
    let reader = new FileReader();
    reader.onload = (e) => {
      var text = reader.result;
      console.log(text);
      console.log(JSON.parse(text))
      this.togglejsonInputSet();
    }
    
    reader.readAsText(files[0]);
  }

  handleFilesModel = (files) =>{
    
    console.log(files)
    let reader = new FileReader();
    reader.onload = (e) => {
      var text = reader.result;
      console.log(text);
      console.log(JSON.parse(text))
      this.togglejsonModelSet();
    }
    
    reader.readAsText(files[0]);
  }

  togglejsonInputSet = () => {
    this.setState({jsonInputSet: !this.state.jsonInputSet});
  }
  
  togglejsonModelSet = () => {
    this.setState({jsonModelSet: !this.state.jsonModelSet});
  }

  render() {
    return(
      <div className="Div-border-yellow Div-side3">
        <ReactFileReader
          fileTypes={[".json"]}
          handleFiles={this.handleFilesInput}>
          <div className ='Font-size15px'>
            <label> Input Json : </label>
            <button className='btn'>Upload</button>
            {this.state.jsonInputSet ? (' ✔') : (' ❌')}
          </div>
        </ReactFileReader>
        <ReactFileReader
          fileTypes={[".json"]}
          handleFiles={this.handleFilesModel}>
          <div className ='Font-size15px'>
          <label> Model Json : </label>
            <button className='btn'>Upload</button>
            {this.state.jsonModelSet ? (' ✔') : (' ❌')}
          </div>
        </ReactFileReader>
        
    </div>
    )
  }
}

class DivOutput extends Component{
  render() {
    return(
      <div className="Div-border-green ">
        <JSONTree data = {json}/>
              
      </div>
    )
  }
}

class DivConfigJson extends Component{
  render() {
    return (
      <div className="Div-border-blue Div-side1">
        {this.props.Text}
        <JSONTree data = {json}/>
      </div>
    )
  }
}

class App extends Component {

  render() {
    return (
      <div>
        <header className="App-header">
          <h1 className="App-title">Interface</h1>
        </header>
        <div>
          <div style={{'overflow':'auto'}} >
            <DivConfigJson Text = {"Teste"}/>
            <DivTreeView/>
            <DivSettings/>
          </div>
          
          <div>
            <DivOutput/>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



